/*---------------- navbar start ----------------*/
.navbar{
  position: fixed;
  width:100%;
  height:6rem;
  z-index: 999;
  display:flex;
  align-self: center;
  justify-content:space-between;
  background-color:#0E0D0D;
}

.navbutton {
  width:100px;
  height:50px;
  margin:auto 1.5rem;
  cursor: pointer;
  display:none;
  
}

#check{
  display:none;
}

.mainlogo{
  margin-left:1.5rem;
  width:150px;
  height:auto;
}

#navul{
  font-family: 'Arial', Regular ;
  width:85%;
  display: flex;
  align-items: baseline;
  justify-content: space-around;
  list-style: none;
  margin:auto 0;  
}

#navul li{
  display:block;
  position: relative;
}
#navul li .dropdown li{
  display:block;
  color:#000;
  line-height: 2;
}

#navul li .dropdown{
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 2px;
  padding:1rem 1rem;
  width:15rem;
  background-color: #fff;
  display:none;
  position: absolute;
  z-index: 999;
}

#navul li .dropdown li:hover{
  color: orangered;
}
#navul li .dropdown li a{
  color: #000;
}
#navul li .dropdown li a:hover{
  color: orangered;
}

#navul li:hover ul.dropdown{
  display: block;
}

#navul a{
  text-decoration: none;
  color:white;
}

#navul a:hover{
  color:orangered;
}
#remove{
  display:none;
}


@media only screen and (max-width: 1200px) {
  .navbutton{
    display:block;
  }

  #navul{
    flex-direction: column;
    position:fixed;
    width:30%;
    height:600px;
    background: #2c3e50;
    top:6rem;
    right:0;
    text-align: center;
    align-items: center;
    margin:auto; 
    display:none;/****remove this to see navul*******************************************************************/
  }

  #navul li{
    display:block;
  }

  #check:checked ~ ul{
    left:0;
  }

  #remove{
    display:block;
    border:1px solid white;
    padding:0.5rem;
    border-radius: 50%;
  }

  

  
}

/*-------------- navbar End ----------- */



